import { registerMvuSchema } from 'https://testingcf.jsdelivr.net/gh/StageDog/tavern_resource/dist/util/mvu_zod.js';
// 为了避免重复，我们先定义一个通用的角色永久属性结构
const CharacterStatsSchema = z.object({
    // 角色头像信息
    // 使用数字编号对应前端头像库中的头像，避免长URL导致提示词长度爆炸
    Avatar: z
        .object({
        current: z.number().int().min(0).prefault(0), // 当前头像编号（对应前端头像库）
        history: z.array(z.number().int().min(0)).prefault([]), // 历史头像编号列表（对应前端头像库）
    })
        .prefault({ current: 0, history: [] }),
    Favor: z.coerce
        .number()
        .transform(v => _.clamp(v, -100, 100))
        .prefault(0), // 好感度，范围-100到100
    Magic: z.coerce.number().prefault(100), // 当前魔力值
    MagicMax: z.coerce.number().prefault(100), // 魔力值上限
    Corruption: z.coerce
        .number()
        .transform(v => _.clamp(v, 0, 100))
        .prefault(0), // 堕落值，范围0到100
    Description: z.array(z.string()).prefault([]), // 角色身份背景介绍（分类数组）
});
// 接着是角色在场景中的临时状态结构
const SceneCharacterSchema = z.object({
    Thought: z.string().prefault(''), // 角色当前想法
    Action: z.string().prefault(''), // 角色当前动作
    State: z.string().prefault('正常'), // 角色状态（正常、受伤、疲劳等）
    isTransformed: z.boolean().prefault(false), // 是否已变身
    Lust: z.coerce
        .number()
        .transform(v => _.clamp(v, 0, 100))
        .prefault(0), // 欲望值，范围0到100
});
// 这是我们将要使用的完整变量结构
export const Schema = z.object({
    当前时间: z.string().prefault('2024年03月17日 星期二 16:45'), // 游戏内当前时间
    location: z.string().prefault('远谷町-垂枝樱学园-二年3班教室'), // 当前位置
    个人线: z
        .record(z.string().describe('角色个人线名称'), z.object({
        状态: z.string().prefault('未开始'), // 个人线状态（未开始、进行中、已完成）
        结局: z
            .record(z.string().describe('结局名称'), z.object({
            说明: z.string().prefault(''), // 结局说明
            达成条件: z.record(z.string().describe('条件名称'), z.boolean().prefault(false)).prefault({}), // 结局达成条件
        }))
            .prefault({}),
    }))
        .prefault({}),
    // 储存所有角色的永久数据
    角色列表: z.record(z.string().describe('角色名'), CharacterStatsSchema).prefault({}),
    // 储存当前场景中出现的角色的临时状态
    在场角色: z.record(z.string().describe('角色名'), SceneCharacterSchema).prefault({}),
    Enemies: z.record(z.string().describe('敌人名'), z.any()).prefault({}), // 敌人列表
    Partners: z.record(z.string().describe('同伴名'), z.any()).prefault({}), // 同伴列表
    // 哥哥你的专属数据
    user: z
        .object({
        SkillPoint: z.coerce.number().prefault(10), // 技能点（侵蚀点）
        Magic: z.coerce.number().prefault(100), // 当前魔力值
        MagicMax: z.coerce.number().prefault(100), // 魔力值上限
        Morality: z.coerce
            .number()
            .transform(v => _.clamp(v, -100, 100))
            .prefault(0), // 善恶值，范围-100到100
        SkillTree: z
            .record(z.string().describe('技能名'), z.object({
            描述: z.string().prefault(''), // 技能描述
            消耗: z.coerce.number().prefault(0), // 学习消耗的技能点
            已学习: z.boolean().prefault(false), // 是否已学习该技能
        }))
            .prefault({}), // 技能树
    })
        .prefault({
        SkillPoint: 10,
        Magic: 100,
        MagicMax: 100,
        Morality: 0,
        SkillTree: {},
    }),
});
$(() => {
    registerMvuSchema(Schema);
});
