// 头像库模块
// 从角色卡绑定的世界书中读取头像数据
// 头像数据存储在名为 "[Data]头像库" 的世界书条目中

export interface AvatarLibrary {
  [characterName: string]: {
    [avatarId: number]: string;
  };
}

// 只保留一个默认头像
const defaultAvatarLibrary: AvatarLibrary = {
  default: {
    0: '', // 空头像
  },
};

let avatarLibrary: AvatarLibrary = { ...defaultAvatarLibrary };
let isInitialized = false;

const AVATAR_ENTRY_NAME = '[Data]头像库';

/**
 * 从世界书条目加载头像库数据
 * 查找名为 "[Data]头像库" 的条目，从其 content 中解析 JSON 数据
 */
async function initAvatarLibrary() {
  if (isInitialized) return;

  try {
    // 获取当前角色卡绑定的世界书
    const charWorldbooks = await getCharWorldbookNames('current');
    const worldbookName = charWorldbooks.primary || charWorldbooks.additional[0];

    if (!worldbookName) {
      console.warn('当前角色卡没有绑定世界书，使用默认头像库');
      isInitialized = true;
      return;
    }

    // 从世界书加载数据
    const entries = await getWorldbook(worldbookName);

    // 查找"[Data]头像库"条目
    const avatarEntry = entries.find((e: WorldbookEntry) => e.name === AVATAR_ENTRY_NAME);

    if (avatarEntry && avatarEntry.content) {
      try {
        // 解析 JSON 内容
        const parsed = JSON.parse(avatarEntry.content);
        if (parsed && typeof parsed === 'object') {
          // 合并默认头像库和保存的数据
          avatarLibrary = {
            ...defaultAvatarLibrary,
            ...parsed,
          };
          console.log('从世界书条目加载头像库成功');
        }
      } catch (parseError) {
        console.error('解析头像库数据失败:', parseError);
      }
    } else {
      console.log('世界书中未找到头像库数据，使用默认头像库');
    }
  } catch (error) {
    console.error('加载头像库失败:', error);
  }

  isInitialized = true;
}

/**
 * 保存头像库数据到世界书条目
 */
export async function saveAvatarLibrary(): Promise<void> {
  try {
    // 获取当前角色卡绑定的世界书
    const charWorldbooks = await getCharWorldbookNames('current');
    const worldbookName = charWorldbooks.primary || charWorldbooks.additional[0];

    if (!worldbookName) {
      console.error('当前角色卡没有绑定世界书，无法保存头像库');
      return;
    }

    // 使用 updateWorldbookWith 更新或创建头像库条目
    await updateWorldbookWith(worldbookName, entries => {
      // 查找现有条目
      const existingIndex = entries.findIndex(e => e.name === AVATAR_ENTRY_NAME);

      const avatarEntry: PartialDeep<WorldbookEntry> = {
        name: AVATAR_ENTRY_NAME,
        enabled: false, // 禁用条目，不进入提示词
        strategy: {
          type: 'constant',
          keys: [],
          keys_secondary: { logic: 'and_any', keys: [] },
          scan_depth: 'same_as_global',
        },
        position: {
          type: 'at_depth',
          role: 'system',
          depth: 0,
          order: 0,
        },
        content: JSON.stringify(avatarLibrary),
        probability: 100,
        recursion: {
          prevent_incoming: true,
          prevent_outgoing: true,
          delay_until: null,
        },
        effect: {
          sticky: null,
          cooldown: null,
          delay: null,
        },
      };

      if (existingIndex >= 0) {
        // 更新现有条目
        entries[existingIndex] = { ...entries[existingIndex], ...avatarEntry };
      } else {
        // 创建新条目
        entries.push(avatarEntry as WorldbookEntry);
      }

      return entries;
    });

    console.log('保存头像库到世界书成功');
  } catch (error) {
    console.error('保存头像库失败:', error);
  }
}

export async function loadAvatarLibrary(): Promise<AvatarLibrary> {
  await initAvatarLibrary();
  return avatarLibrary;
}

export function getAvatarLibrary(): AvatarLibrary {
  return avatarLibrary;
}

export function getCharacterAvatarLibrary(characterName: string): Record<number, string> {
  return avatarLibrary[characterName] || avatarLibrary.default;
}

export function getAvatarUrl(characterName: string, avatarId: number): string {
  const charAvatarLibrary = getCharacterAvatarLibrary(characterName);
  return charAvatarLibrary[avatarId] || '';
}

export async function addAvatar(characterName: string, avatarId: number, url: string): Promise<void> {
  if (!avatarLibrary[characterName]) {
    avatarLibrary[characterName] = {};
  }
  avatarLibrary[characterName][avatarId] = url;
  await saveAvatarLibrary();
}

export async function deleteAvatar(characterName: string, avatarId: number): Promise<void> {
  if (avatarLibrary[characterName] && avatarLibrary[characterName][avatarId] !== undefined) {
    delete avatarLibrary[characterName][avatarId];
    await saveAvatarLibrary();
  }
}

export function getNextAvatarId(characterName: string): number {
  const charAvatarLibrary = getCharacterAvatarLibrary(characterName);
  const existingIds = Object.keys(charAvatarLibrary)
    .map(Number)
    .filter(id => id > 0);
  if (existingIds.length === 0) {
    return 1;
  }
  return Math.max(...existingIds) + 1;
}

export async function clearAvatarLibrary(): Promise<void> {
  avatarLibrary = { ...defaultAvatarLibrary };
  await saveAvatarLibrary();
}

export function exportAvatarLibrary(): string {
  return JSON.stringify(avatarLibrary, null, 2);
}

export async function importAvatarLibrary(jsonString: string): Promise<boolean> {
  try {
    const imported = JSON.parse(jsonString);
    if (typeof imported === 'object' && imported !== null) {
      avatarLibrary = { ...defaultAvatarLibrary, ...imported };
      await saveAvatarLibrary();
      return true;
    }
    return false;
  } catch (error) {
    console.error('导入头像库失败:', error);
    return false;
  }
}

// 初始化
initAvatarLibrary();
